@font-face {
    font-family: 'BlinkGaston';
    src: url('../Fonts/BlinkGaston.otf');
    }

@font-face {
    font-family: 'CocogooseLight';
    src: url('../Fonts/CocogooseLight.ttf');
    }
    
.about-container {
    width: 60%;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.about-paragraph-container {
    width: auto;
    margin-left: 10px;
}

/* #808080; */
.about-paragraph-text {
    color: #DCDCDC;
    font-size: 10px;
    font-family: 'CocogooseLight';
}

.about-text-container {
    width: auto;
    margin-right: 10px;
}

.about-large-text {
    font-size: 20px;
    font-family: 'CocogooseLight';
    color: #DCDCDC;
    align-items: right;
}

h1 {
    text-align: right;
    margin-top: 10px;
    margin-bottom: 10px;
}

.numbers {
    font-family: Arial, Helvetica, sans-serif
}