@font-face {
    font-family: 'BlinkGaston';
    src: url('../Fonts/BlinkGaston.otf');
    }

@font-face {
    font-family: 'CocogooseLight';
    src: url('../Fonts/CocogooseLight.ttf');
    }

.welcome-container {
    padding-top: 100px;
    width: 60%;
    margin: auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.welcome-text-container {
    width: 50%;
}

.welcome-large-text {
    font-size: 40px;
    font-family: 'CocogooseLight';
    color: #DCDCDC;
}

h1 {
    margin-top: 0px;
    margin-bottom: 0px;
    line-height: .85;
}

.bret-text {
    margin-top: -40px;
    margin-bottom: -40px;
    font-size: 130px;
    font-family: 'BlinkGaston';
    color: #89cff0;
}

/* #808080; */
.welcome-small-text {
    margin-top: 50px;
    color: #DCDCDC;
    font-size: 15px;
    font-family: 'CocogooseLight';
}

.welcome-image-container {
    width: 50%;
    z-index:-1;
}

.welcome-image {
    height: 600px;
    pointer-events: none;
    user-select: none;
    mask-image: linear-gradient(black, 30%, transparent);
    filter: grayscale(30%);
}